export default {
	components: {
	},
	props: [
		'value',
		'type',
		'isPassword',
		'placeholder',
		'placeholderStyle',
		'placeholderClass',
		'disabled',
		'cursorSpacing',
		'focus',
		'confirmType',
		'confirmHold',
		'cursor',
		'adjustPosition',
		'maxlength',
		'inputColor',
		'inputFontSize',
		'inputLineHeight',
		'inputLine',
		'inputWidth',
		'inputHeight',
		'inputTAlign',
		'inputPadding',
		'inputBG',
		'inputBorder',
		'inputBStyle',
		'indexNum',
		'inputBoxMargin',
		'inputLineStyle',
		'inputLineYShow',
		'inputSortShow',
		'inputType',
		'dataList'
		],
	data() {
		return {
			dataLists: this.dataList ? this.dataList.dataList : null,
			disableds: false,
			inputMargin: '1px',
			inputBGs: '', // 默认
			inputBorders: '', // 默认
			inputBStyles: '', // 默认
			valueText: this.value
		};
	},
	created() {
		if (this.disabled === true && this.inputType === 2) {
			console.log('当前触发最大起批量！');
			this.disableds = true;
			return
		};
		if (this.inputType) {
			if(this.inputType === 1 || this.inputType === 3) {
				this.inputBorders = this.inputBorder;
				this.inputMargin = '';
			}
		};
	},
	watch: {
		value: {
			handler(a, b) {
				console.log(a, b, '--------------------------------------监听valueText变化---------------------------------------');
				this.valueText = a;
			},
			deep: true
		},
		dataList: {
			handler(a, b) {
				console.log(a, b, '--------------------------------------监听dataList变化---------------------------------------');
				this.dataLists = a.dataList;
				
			},
			deep: true
		},
	},
	methods: {
		// 当键盘输入时，触发input事件
		onInput(e) {
			console.log(e, 'input返回值');
		},
		// 输入框聚焦时触发
		onFocus(e) {
			if (this.disabled === true && this.inputType === 2) {
				console.log('当前触发最大起批量！');
				this.disableds = true;
				return
			} else if (this.disabled === true && this.inputType === 1) {
				console.log('触发最下起批量！');
			}
		},
		// 输入框失去焦点时触发
		onBlur(e) {
			this.valueText = Number(e.detail.value);
			this.$emit('tap-Input',[this.valueText, this.indexNum]);
			uni.pageScrollTo({
			scrollTop: 0,
			duration: 0
			});
		},
		// 点击完成按钮时触发
		onConfirm(e) {
			console.log(e, 'input返回值');
			this.valueText = Number(e.detail.value);
			this.$emit('tap-Input',[this.valueText, this.indexNum]);
			uni.pageScrollTo({
			scrollTop: 0,
			duration: 0
			});
		}
	}
};
